<script setup>
import { onMounted } from 'vue';
import { useRouter } from 'vue-router'
import TopNav from '../components/topNavs.vue'
//import VideoPlayer from '../components/videoPlayer.vue'
import ArticleComponent from '../components/articleComponent.vue';
import homePageJson from '../assets/json/homepage.json';
import { reactive ,ref } from 'vue';
const dataInfo  = reactive({
    obj:[]
})
dataInfo.obj = homePageJson.codes;
const router = useRouter()
onMounted(() => {
    const cvs = document.getElementById("mcb");
    var ctx = cvs.getContext("2d");
    var w, h, lines, conf, bgDots, dat, generateLines;
    window.onresize = function () {
        w = ctx.canvas.width = window.innerWidth;
        h = ctx.canvas.height = window.innerHeight;
    };
    window.onresize();

    lines = [];

    conf = {
        hue: 200,
        shadow: false,
        width: 1,
        length: 1,
        emitNum: 2,
        speed: 1,
        opacity: 1,
        maxLines: 20
    };

    bgDots = [{
        rad: (w + h) / 2,
        x: w / 2,
        y: 0,
        hue: 0
    },
    {
        rad: (w + h) / 2,
        x: 0,
        y: h,
        hue: -45
    },
    {
        rad: (w + h) / 2,
        x: w,
        y: h,
        hue: -90
    }
    ];
    function emitLine() {
        if (conf.maxLines < lines.length)
            return;
        for (var i = 0; i < conf.emitNum; i++) {
            var rx = Math.random() * w + 100;
            var ry = Math.random() * h - 100;

            lines.push({
                x1: rx,
                y1: ry,
                x2: rx,
                y2: ry,
                length: (Math.random() * (260 - 80) + 80) * conf.length,
                width: (Math.random() * (15 - 5) + 5) * conf.width,
                v1: (Math.random() * (4 - 2) + 2) * conf.speed,
                v2: (Math.random() * (1 - 0.5) + 0.5) * conf.speed,
                half: false,
                hue: Math.random() * 100
            });
        }
    }

    function drawBackground() {
    
        ctx.globalCompositeOperation = "lighter";
        for (var i = 0; i < bgDots.length; i++) {
            var grd = ctx.createRadialGradient(bgDots[i].x, bgDots[i].y, 0, bgDots[i].x, bgDots[i].y, bgDots[i].rad);
            grd.addColorStop(0, "hsla(" + (conf.hue + bgDots[i].hue) + ", 0%, 5%, 0.3)");
            grd.addColorStop(1, "hsla(" + (conf.hue + bgDots[i].hue) + ", 0%, 5%, 0)");
            ctx.beginPath();
            ctx.arc(bgDots[i].x, bgDots[i].y, bgDots[i].rad, 0, Math.PI * 2);
            ctx.fillStyle = grd;
            ctx.fill();
            ctx.closePath();
        }
        
    }

    function drawLines() {
        //ctx.globalCompositeOperation = "lighter";
        for (var i = 0; i < lines.length; i++) {
            ctx.lineWidth = lines[i].width;
            ctx.beginPath();
            ctx.moveTo(lines[i].x1, lines[i].y1);
            ctx.lineTo(lines[i].x2, lines[i].y2);
            //console.log("hsla(" + (conf.hue - lines[i].hue) + ", 0%, 15%, " + (conf.opacity) + ")")
            //ctx.strokeStyle = "hsl(120,100%,50%,0.3)";
            ctx.strokeStyle = "#111836"
            ctx.lineCap = "round";
            ctx.stroke();
            ctx.closePath();

            if (lines[i].half == false) {
                lines[i].x1 -= lines[i].v1;
                lines[i].y1 += lines[i].v1;
                lines[i].x2 -= lines[i].v2;
                lines[i].y2 += lines[i].v2;
                if (dist(lines[i].x1, lines[i].y1, lines[i].x2, lines[i].y2) > lines[i].length) {
                    lines[i].half = true;
                }
            } else {
                lines[i].x1 -= lines[i].v2;
                lines[i].y1 += lines[i].v2;
                lines[i].x2 -= lines[i].v1;
                lines[i].y2 += lines[i].v1;
            }
        }
    }

    function clear() {
        ctx.globalCompositeOperation = "source-over";
        ctx.beginPath();
        ctx.fillStyle = "#EEEEEE";
        ctx.fillRect(0, 0, w, h);
        ctx.closePath();
    }

    function checkLines() {
        emitLine();
        for (var i = 0; i < lines.length; i++) {
            if (lines[i].half == true && dist(lines[i].x1, lines[i].y1, lines[i].x2, lines[i].y2) <= 10) {
                lines[i] = lines[lines.length - 1];
                lines.pop();
            } else if (lines[i].x1 < 0 && lines[i].x2 < 0 && lines[i].y1 > h && lines[i].y2 > h) {
                lines[i] = lines[lines.length - 1];
                lines.pop();
            }
        }
    }

    generateLines = setInterval(checkLines, 10);

    function dist(x1, y1, x2, y2) {
        return Math.sqrt(Math.pow((x2 - x1), 2) + Math.pow((y2 - y1), 2));
    }

    function render() {
        clear();
        //drawBackground();
        drawLines();
        requestAnimationFrame(render);
    }
    render();

   
})
function goArticle(item){
    window.open(item.url)
 }

</script>

